#customize {
    margin: auto;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    width: 350px;
    max-width: 380px;
    padding: 80px 40px 40px;
    background: #000000;
    border-radius: 10px;
    color: #fff;
    box-shadow: 0 15px 25px rgba(0,0,0,0.5);
    visibility: hidden;
}
#customize::before {
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(255,255,255, 0.08);
    transform: skewX(-26deg);
    transform-origin: bottom left;
    border-radius: 10px;
    pointer-events: none;
}
#customize img {
    position: absolute;
    top: -50px;
    left: calc(50% - 50px);
    width: 100px;
    background: rgba(255,255,255, 0.8);
    border-radius: 50%;
}
#customize h2 {
    text-align: center;
    letter-spacing: 1px;
    margin-bottom: 2rem;
    color: #FFFFFF;
}
#customize .input-group {
    position: relative;
}
#customize .input-group input {
    width: 100%;
    padding: 10px 0;
    font-size: 1rem;
    letter-spacing: 1px;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    background-color: transparent;
    color: inherit;
}
#customize .input-group label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 0;
    font-size: 1rem;
    pointer-events: none;
    transition: .3s ease-out;
}
#customize .input-group input:focus + label,
#customize .input-group input:valid + label {
    transform: translateY(-18px);
    color: #FFFFFF;
    font-size: .8rem;
}
.submit-btn {
    display: block;
    margin-left: auto;
    border: none;
    outline: none;
    background: #FFFFFF;
    font-size: 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}
.close {
    position: absolute;
    right: 1.5rem;
    top: 0.5rem;
    font-size: 2rem;
    font-weight: 900;
    text-decoration: none;
    color: inherit;
}


button{
    font-size: 20px;
    display:inline-block;
    padding:0.3em 1.2em;
    margin:0 0.3em 0.3em 0;
    border-radius:2em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color: #000000;
    background-color:#4eb5f1;
    text-align:center;
    transition: all 0.2s;
    border: none;
}

button:hover{
    background-color:#4095c6;
}

#customizebtn{
    width: 230px;
    height: 40px;
}

.creditDebit{
    height: 40px;
    width: 110px;
}


@media all and (max-width:30em){
    button{
    display:block;
    margin:0.2em auto;
    }
}



